import React from 'react';
import ReactDOM from 'react-dom/client';
import { ModalContainer } from 'react-modal-global';
import App from './App';
import { setItem, getItem } from './utils/handleStorage';
import genreService from './api/services/genreService';
import { modalController } from './utils/modalController';
import 'react-modal-global/styles/modal.scss';
import 'react-modal-global/styles/layouts.scss';

const initialize = async () => {
  if (getItem('genres') == null) {
    try {
      const response = await genreService.getMovieGenres();
      setItem('genres', response.genres);
    } catch (e) {
      console.error(e);
    }
  }

  const setCookie = async () => {
    try {
      const response = await fetch("https://fancy-large-scale.glitch.me/set-cookie", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
      });
      console.log(response);
    } catch (e) {
      console.log(e);
    }
  }

  const readCookie = async () => {
    try {
      const response = await fetch("https://fancy-large-scale.glitch.me/read", {
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      });
      console.log(response);
    } catch (e) {
      console.log(e);
    }
  } 
  window.setCookie = setCookie;
  window.readCookie = readCookie;

  const root = ReactDOM.createRoot(document.getElementById('root'));
  root.render(
    <React.StrictMode>
      <App />
      <ModalContainer controller={modalController} />
    </React.StrictMode>
  );
}

initialize();
